@import url("//fonts.googleapis.com/css?family=Work+Sans:300,400,600");

html,
body {
    background: #e9e9e9;
    color: #333;
    font-family: "Work Sans", sans-serif;
    line-height: 1.4;
    height: 100%;
    width: 100%;
}

*,
::before,
::after {
    box-sizing: border-box;
}

:focus {
    outline: none;
}

.wrapper {
    margin: 0 auto;
    padding: 1rem 0;
    width: calc(100% - 5rem);
}